import React, { useState, useEffect } from 'react';
import { useParams, Link } from 'react-router-dom';
import axios from 'axios';
import { ArrowRight, Diamond, Award, Shield, Heart, Star, ChevronDown, Phone, Mail, Sparkles, Check, Gem, ShoppingBag, MessageCircle, HelpCircle, ChevronUp } from 'lucide-react';
import { useCart } from 'react-use-cart';
import ProductImageGallery from '../components/ProductImageGallery';

const ProductDetail = () => {
  const { id } = useParams();
  const [product, setProduct] = useState(null);
  const [loading, setLoading] = useState(true);
  const [relatedProducts, setRelatedProducts] = useState([]);
  const [selectedMetal, setSelectedMetal] = useState('14K');
  const [priceRange, setPriceRange] = useState({ min: 2500, max: 12500 });
  const [caratExpanded, setCaratExpanded] = useState(false);
  const [sizeExpanded, setSizeExpanded] = useState(false);
  
  // Cart functionality
  const { addItem } = useCart();

  useEffect(() => {
    // Scroll to top when component mounts or ID changes
    window.scrollTo({ top: 0, behavior: 'smooth' });
    fetchProduct();
  }, [id]);

  const fetchProduct = async () => {
    try {
      const response = await axios.get(`/products/${id}`);
      setProduct(response.data);
      
      // Fetch related products from the same category
      if (response.data.category_id) {
        const relatedResponse = await axios.get(`/products?category_id=${response.data.category_id}&limit=4`);
        setRelatedProducts(relatedResponse.data.filter(p => p.id !== parseInt(id)));
      }
    } catch (error) {
      console.error('Error fetching product:', error);
    } finally {
      setLoading(false);
    }
  };

  if (loading) {
    return (
      <div className="min-h-screen flex items-center justify-center bg-gray-50" dir="rtl">
        <div className="text-center">
          <div className="w-16 h-16 bg-gradient-to-br from-yellow-400 to-yellow-600 rounded-full flex items-center justify-center mx-auto mb-4 animate-pulse">
            <Diamond className="w-8 h-8 text-white" />
          </div>
          <p className="text-gray-600">  ...</p>
        </div>
      </div>
    );
  }

  if (!product) {
    return (
      <div className="min-h-screen flex items-center justify-center bg-gray-50" dir="rtl">
        <div className="text-center p-8">
          <div className="w-20 h-20 bg-gray-200 rounded-full flex items-center justify-center mx-auto mb-6">
            <Diamond className="w-10 h-10 text-gray-400" />
          </div>
          <h2 className="text-2xl font-light text-gray-900 mb-4">  </h2>
          <p className="text-gray-600 mb-8">      </p>
          <Link to="/products" className="bg-yellow-500 hover:bg-yellow-600 text-white px-8 py-3 rounded-full transition-colors">
             
          </Link>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gray-50" dir="rtl">
      {/* Ultra Clean Styles */}
      <style jsx="true">{`
        .ultra-clean-card {
          background: white;
          border-radius: 0;
          box-shadow: none;
          border: none;
        }
        
        .category-pill {
          background: linear-gradient(135deg, #fef3c7, #fcd34d);
          color: #92400e;
          padding: 8px 16px;
          border-radius: 20px;
          font-size: 14px;
          font-weight: 600;
          display: inline-flex;
          align-items: center;
          gap: 6px;
          margin-bottom: 16px;
        }
        
        .metal-button {
          width: 70px;
          height: 36px;
          border: 1px solid #d1d5db;
          border-radius: 4px;
          background: white;
          display: flex;
          align-items: center;
          justify-content: center;
          cursor: pointer;
          transition: all 0.2s ease;
          font-weight: 500;
          font-size: 14px;
          color: #6b7280;
        }
        
        .metal-button.selected {
          background: #d4af37;
          border-color: #d4af37;
          color: white;
        }
        
        .collapsible-section {
          border: 1px solid #e5e7eb;
          border-radius: 8px;
          overflow: hidden;
          margin-bottom: 16px;
        }
        
        .collapsible-header {
          background: white;
          padding: 16px;
          border-bottom: 1px solid #e5e7eb;
          cursor: pointer;
          display: flex;
          justify-content: between;
          align-items: center;
          transition: background-color 0.2s ease;
        }
        
        .collapsible-header:hover {
          background: #f9fafb;
        }
        
        .spec-row {
          display: flex;
          justify-content: space-between;
          align-items: center;
          padding: 16px;
          border-bottom: 1px solid #f1f5f9;
          background: white;
        }
        
        .spec-row:last-child {
          border-bottom: none;
        }
        
        .spec-label {
          display: flex;
          align-items: center;
          gap: 8px;
          color: #4b5563;
          font-size: 14px;
        }
        
        .spec-value {
          color: #111827;
          font-weight: 600;
          font-size: 16px;
        }
        
        .help-icon {
          width: 16px;
          height: 16px;
          color: #d4af37;
          cursor: help;
        }
        
        .image-container {
          background: white;
          padding: 32px;
          display: flex;
          align-items: center;
          justify-content: center;
          min-height: 500px;
        }
        
        .breadcrumb-container {
          background: white;
          border-bottom: 1px solid #f1f5f9;
          padding: 16px 0;
        }
        
        /* Mobile optimizations */
        @media (max-width: 768px) {
          .metal-button {
            width: 60px;
            height: 32px;
            font-size: 12px;
          }
          
          .image-container {
            padding: 16px;
            min-height: 300px;
          }
        }
      `}</style>

      {/* Ultra Clean Breadcrumb */}
      <div className="breadcrumb-container">
        <div className="max-w-6xl mx-auto px-4">
          <nav className="flex items-center space-x-2 space-x-reverse text-sm text-gray-500">
            <Link to="/" className="hover:text-gray-700 transition-colors"> </Link>
            <ArrowRight className="w-4 h-4 rotate-180" />
            <Link to="/products" className="hover:text-gray-700 transition-colors"></Link>
            <ArrowRight className="w-4 h-4 rotate-180" />
            <span className="text-gray-900 font-medium truncate">{product.name}</span>
          </nav>
        </div>
      </div>

      <div className="max-w-6xl mx-auto">
        <div className="grid grid-cols-1 lg:grid-cols-2">
          {/* Ultra Clean Product Image */}
          <div className="image-container">
            <ProductImageGallery 
              images={product.images || product.image_url} 
              productName={product.name}
              className="w-full max-w-md"
            />
          </div>

          {/* Ultra Clean Product Info */}
          <div className="p-6 bg-white">
            {/* Category Badge */}
            {product.category && (
              <div className="category-pill">
                <Diamond className="w-4 h-4" />
                : {product.category.name}
              </div>
            )}

            {/* Product Title */}
            <h1 className="text-2xl font-light text-gray-900 mb-6 leading-relaxed">
              {product.name}
            </h1>

            {/* Metal Selection */}
            <div className="mb-6">
              <h3 className="text-gray-700 mb-3 font-medium">/   </h3>
              <div className="flex gap-2">
                {['14K', '14K', '14K'].map((metal, index) => (
                  <button
                    key={index}
                    onClick={() => setSelectedMetal(metal)}
                    className={`metal-button ${selectedMetal === metal && index === 2 ? 'selected' : ''}`}
                  >
                    {metal}
                  </button>
                ))}
              </div>
            </div>

            {/* Carat Selection - Collapsible */}
            <div className="collapsible-section">
              <div 
                className="collapsible-header"
                onClick={() => setCaratExpanded(!caratExpanded)}
              >
                <div className="flex items-center gap-2">
                  <HelpCircle className="help-icon" />
                  <span className="text-gray-700">/   </span>
                </div>
                {caratExpanded ? <ChevronUp className="w-5 h-5" /> : <ChevronDown className="w-5 h-5" />}
              </div>
              {caratExpanded && (
                <div className="p-4 bg-gray-50">
                  <p className="text-sm text-gray-600">     </p>
                </div>
              )}
            </div>

            {/* Diamond Specifications */}
            <div className="mb-6">
              {product.color_grade && (
                <div className="spec-row">
                  <div className="spec-label">
                    <HelpCircle className="help-icon" />
                    <span>: E</span>
                  </div>
                  <div className="spec-value">{product.color_grade}</div>
                </div>
              )}
              
              {product.clarity_grade && (
                <div className="spec-row">
                  <div className="spec-label">
                    <HelpCircle className="help-icon" />
                    <span>: VS</span>
                  </div>
                  <div className="spec-value">{product.clarity_grade}</div>
                </div>
              )}
            </div>

            {/* Ring Size Selection - Collapsible */}
            <div className="collapsible-section">
              <div 
                className="collapsible-header"
                onClick={() => setSizeExpanded(!sizeExpanded)}
              >
                <div className="flex items-center gap-2">
                  <span className="text-gray-700"> </span>
                </div>
                {sizeExpanded ? <ChevronUp className="w-5 h-5" /> : <ChevronDown className="w-5 h-5" />}
              </div>
              {sizeExpanded && (
                <div className="p-4 bg-gray-50">
                  <select className="w-full p-3 border border-gray-300 rounded-md">
                    <option> </option>
                    <option>50</option>
                    <option>52</option>
                    <option>54</option>
                    <option>56</option>
                    <option>58</option>
                    <option>60</option>
                  </select>
                </div>
              )}
            </div>

            {/* Price Display - Simple */}
            <div className="py-6 border-t border-gray-100">
              <div className="text-center">
                <div className="text-3xl font-light text-gray-900 mb-2">
                  {priceRange.min.toLocaleString()} - {priceRange.max.toLocaleString()}
                </div>
                <div className="text-gray-500 text-sm">  "</div>
              </div>
            </div>

            {/* Action Buttons - Minimal */}
            <div className="mt-6">
              <button 
                onClick={() => addItem(product)}
                className="w-full bg-green-600 hover:bg-green-700 text-white py-3 px-6 rounded-md font-medium transition-colors mb-3"
              >
                 
              </button>
              
              <button className="w-full border border-gray-300 text-gray-700 py-3 px-6 rounded-md font-medium hover:bg-gray-50 transition-colors flex items-center justify-center gap-2">
                <Heart className="w-5 h-5" />
                 
              </button>
            </div>

            {/* WhatsApp Contact - Minimal */}
            <div className="mt-6 p-4 bg-green-50 border border-green-200 rounded-lg">
              <div className="flex items-center justify-between">
                <div className="flex items-center gap-3">
                  <div className="w-10 h-10 bg-gray-200 rounded-full flex items-center justify-center">
                    <img 
                      src="/api/placeholder/40/40" 
                      alt=" " 
                      className="w-full h-full object-cover rounded-full"
                    />
                  </div>
                  <div className="text-right">
                    <div className="text-sm font-medium text-gray-800">/  ?</div>
                    <div className="text-xs text-gray-600">   (  )</div>
                  </div>
                </div>
                <button className="bg-green-500 hover:bg-green-600 text-white p-2 rounded-full transition-colors">
                  <MessageCircle className="w-5 h-5" />
                </button>
              </div>
            </div>

            {/* Benefits - Minimal */}
            <div className="mt-6 pt-6 border-t border-gray-100">
              <div className="grid grid-cols-3 gap-4 text-center text-xs text-gray-600">
                <div className="flex flex-col items-center gap-1">
                  <Check className="w-4 h-4 text-green-500" />
                  <span> </span>
                </div>
                <div className="flex flex-col items-center gap-1">
                  <Check className="w-4 h-4 text-green-500" />
                  <span>  30 </span>
                </div>
                <div className="flex flex-col items-center gap-1">
                  <Check className="w-4 h-4 text-green-500" />
                  <span> </span>
                </div>
              </div>
            </div>
          </div>
        </div>

        {/* Related Products - Minimal */}
        {relatedProducts.length > 0 && (
          <div className="border-t border-gray-100 p-6 bg-white">
            <h2 className="text-xl font-light text-gray-900 text-center mb-6"> </h2>
            
            <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
              {relatedProducts.map((relatedProduct) => (
                <Link
                  key={relatedProduct.id}
                  to={`/products/${relatedProduct.id}`}
                  className="block border border-gray-200 rounded-lg overflow-hidden hover:border-gray-300 transition-colors"
                >
                  <ProductImageGallery 
                    images={relatedProduct.images || relatedProduct.image_url} 
                    productName={relatedProduct.name}
                    className="w-full h-40"
                    showNavigation={false}
                  />
                  
                  <div className="p-3">
                    <h3 className="text-sm text-gray-900 mb-2 line-clamp-2">{relatedProduct.name}</h3>
                    <div className="text-lg font-semibold text-gray-900">
                      {relatedProduct.price.toLocaleString()}
                    </div>
                  </div>
                </Link>
              ))}
            </div>
          </div>
        )}
      </div>
    </div>
  );
};

export default ProductDetail; 